import { Link } from 'react-router-dom';
import Logo from '../assets/images/GYmLIFe-LOGO.png';
import styled from 'styled-components';
import {Nav} from './index';

const Footer = (props) => {
    const Footer = styled.footer`
        background-color: #000;
        border-bottom: 1px solid #fff;
    `
    const Container = styled.div`
        width: 80%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
            width: 180px;
            @media (max-width: 767px) {
                width: 160px;
            }
        }
    `
    const Return = styled.div`
        @media (min-width: 768px) {
            display: none;
        }
        font-size: 20px;
        font-weight: bold;
        align-self: center;
        &::after {
            content: '>';
            display: inline-block;
            transform: rotate(-90deg);
            font-size: 30px;
        }
        a {
            position: relative;
            display: inline-block;
            &::after {
                position: absolute;
                bottom: -4px;
                left: 0;
                content: '';
                width: 100%;
                height: 2px;
                background: #fff;
                transform: scale(0, 1);
                transform-origin: center top;
                transition: transform .5s;
            }
            &:hover::after {
                transform: scale(1, 1);
            }
        }
    `

    return (
        <Footer>
            <Container>
                <Link to={'/'}>
                    <div onClick={props.close}><img src={Logo} alt="GYmLIFe" /></div>
                </Link>
                <Nav contact={props.contact}/>
                <Return><a href="#">トップへ戻る</a></Return>
            </Container>
        </Footer>
    )
}

export default Footer;