import { Link } from 'react-router-dom';
import Logo from '../assets/images/GYmLIFe-LOGO.png';
import styled from 'styled-components';
import { Nav, HumburgerButton } from './index';

const Header = (props) => {
    const Header = styled.header`
        background-color: #000;
        border-top: 1px solid #fff;
        position: fixed;
        width: 100%;
        z-index: 1;
    `
    const Container = styled.div`
        width: 80%;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
            width: 180px;
            @media (max-width: 767px) {
                width: 160px
            }
        }
    `

    return (
        <Header>
            <Container>
                <Link to={'/'}>
                    <div onClick={props.close}><img src={Logo} alt="GYmLIFe" /></div>
                </Link>
                <Nav contact={props.contact} />
                <HumburgerButton open={props.open} setOpen={props.setOpen} />
            </Container>
        </Header>
    )
}

export default Header;