import React from 'react';
import './assets/styles/style.css';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import {Header, Body, HumburgerMenu, Facility, Plan, Trainer, Supplements, Last, Footer, ContactModal} from './Components/index';
import ScrollToTop from './Components/ScrollToTop';

export default class App  extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            humburger: false
        }
        this.handleClickOpen = this.handleClickOpen.bind(this);
        this.handleClickClose = this.handleClickClose.bind(this);
        this.toggleOpen = this.toggleOpen.bind(this);
        this.humburgerClose = this.humburgerClose.bind(this);
    }

    handleClickOpen = () => {
        this.setState({open: true});
    }
    handleClickClose = () => {
        this.setState({open: false});
    }
    toggleOpen = () => {
        this.setState({humburger: !this.state.humburger});
    }
    humburgerClose = () => {
        this.setState({humburger: false});
    }

    render() {
        return (
        <div>
            <Router>
                <ScrollToTop />
                <Header contact={this.handleClickOpen} open={this.state.humburger} setOpen={this.toggleOpen} close={this.humburgerClose} />
                <HumburgerMenu contact={this.handleClickOpen} open={this.state.humburger} setOpen={this.toggleOpen} />
                    <Switch>
                        <Route exact path="/" render={() => <Body contact={this.handleClickOpen} />} />
                        <Route path="/facility" component={Facility} />
                        <Route path="/plan" component={Plan} />
                        <Route path="/trainer" component={Trainer} />
                        <Route path="/supplements" component={Supplements} />
                        <Route component={Body} />
                    </Switch>
                    <Last contact={this.handleClickOpen} />
                <Footer contact={this.handleClickOpen} close={this.humburgerClose}/>
                <ContactModal open={this.state.open} handleClose={this.handleClickClose} />
            </Router>
        </div>
        )
    }
}