import {useState} from 'react';
import styled from 'styled-components';
import {ConnectItem} from '../index';
import imgFacility from '../../assets/images/ジム風景.jpg';
import imgPlan from '../../assets/images/ドレッドミル・女性.jpg';
import imgTrainer from '../../assets/images/トレーナーC.jpg';
import imgSupplements from '../../assets/images/プロテインHuel.jpg';

const Connect = () => {
    const [imgUrl, setImgUrl] = useState('');
    const setImage = (image) => {
        setImgUrl(image);
    }

    const ConnectSection = styled.div`
        background-image: url(${imgUrl});
        background-color: black;
        background-size: cover;
        background-position: center;
    `
    const Mask = styled.div`
        background-color: rgba(0, 0, 0, 0.5);
    `
    const ConnectWrap = styled.div`
        padding: 25px 37px 25px 37px;
        display: flex;
        flex-wrap: wrap;
    `
    const datas = [
        {image: imgFacility, title: 'facility', discription: '施設紹介'},
        {image: imgPlan, title: 'plan', discription: 'フィットネスプラン'},
        {image: imgTrainer, title: 'trainer', discription: 'トレーナー紹介'},
        {image: imgSupplements, title: 'supplements', discription: 'サプリメント'}
    ]

    return (
        <ConnectSection>
            <Mask>
                <ConnectWrap>
                    {datas.map((data => {
                        return (
                            <ConnectItem
                                image={data.image}
                                title={data.title}
                                discription={data.discription}
                                setImage={(image) => setImage(image)}
                            />
                        )
                    }))}
                </ConnectWrap>
            </Mask>
        </ConnectSection>
    )
}

export default Connect;