import styled from "styled-components";

const HumburgerButton = (props) => {
    const Button = styled.div`
        display: none;
        @media (max-width: 767px) {
            display: initial;
        }
    `

    return (
        <Button>
            <div className="humburger-btn" onClick={props.setOpen} aria-expanded={props.open} >
                <span></span>
                <span></span>
                <span></span>
            </div>
        </Button>
    )
}

export default HumburgerButton;