import React from 'react';
import styled from 'styled-components';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Input } from '../index';

export default class ContactModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            text: ''
        }
        this.inputName = this.inputName.bind(this);
        this.inputEmail = this.inputEmail.bind(this);
        this.inputText = this.inputText.bind(this);
    }

    inputName = (event) => {
        this.setState({name: event.target.value});
    }
    inputEmail =(event) => {
        this.setState({email: event.target.value});
    }
    inputText = (event) => {
        this.setState({text: event.target.value});
    }

    render () {
        const contactdatas = [
            {label: 'お名前（必須）', multiline: false, value: this.state.name, type: 'text', rows: 1, onChange: this.inputName},
            {label: 'メールアドレス', multiline: false, value: this.state.email, type: 'email', rows: 1, onChange: this.inputEmail},
            {label: 'お問い合わせ内容', multiline: true, value: this.state.text, type: 'text', rows: 5, onChange: this.inputText},
        ]

        return (
            <Dialog
                open={this.props.open}
                onClose={this.props.handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
            <DialogTitle id="alert-dialog-title">Contact</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {contactdatas.map((data => {
                        return (
                            <Input
                                label={data.label}
                                multiline={data.multiline}
                                value={data.value}
                                type={data.type}
                                rows={data.rows}
                                onChange={data.onChange}
                            />
                        )
                    }))}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={this.props.handleClose} color="primary">
                    キャンセル
                </Button>
                <Button onClick={this.props.handleClose} color="primary" autoFocus>
                    送信
                </Button>
            </DialogActions>
            </Dialog>
        )
    }
}