import { SupplementsItem, Button } from '../index';
import styled from 'styled-components';

const SupplementsList = (props) => {
    const List = styled.div`
        width: 80%;
        margin: 0 auto;
        padding-top: 30px;
    `
    const SupplementsBtn = styled.div`
        text-align: center;
        padding: 60px 0;
    `

    return (
        <List>
            {props.datas.map((data => {
                return (
                    <SupplementsItem
                        image={data.image}
                        title={data.title}
                        content={data.content}
                        included={data.included}
                        price={data.price}
                    />
                )
            }))}
            <SupplementsBtn><Button btn={'もっと見る'} /></SupplementsBtn>
        </List>
    )
}

export default SupplementsList;