import styled from 'styled-components';
import { NavLink } from 'react-router-dom';


const Nav = (props) => {
    const NavTop = styled.ul`
        display: flex;
        margin-right: 100px;
        @media (max-width: 767px) {
            display: none;
        }
    `
    const NavBottom = styled.ul`
        display: flex;
        padding-top: 10px;
        margin-left: 50px;
        @media (max-width: 767px) {
            display: none;
        }
    `
    const NavItem = styled.li`
        text-transform: uppercase;
        font-size: 20px;
        cursor: pointer;
        &:not(:last-child) {
            padding-right: 50px;
        }
        span {
            position: relative;
            display: inline-block;
            &::after {
                position: absolute;
                bottom: -1px;
                left: 0;
                content: '';
                width: 100%;
                height: 2px;
                background: #fff;
                transform: scale(0, 1);
                transform-origin: center top;
                transition: transform .5s;
            }
            &:hover::after {
                transform: scale(1, 1);
            }
        }
        .active {
            border-bottom: 2px solid #fff;
            padding-bottom: 2px;
        }
    `
    return (
        <nav>
            <NavTop>
                <NavItem>
                    <NavLink to={`/facility`}><span>facility</span></NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to={'/plan'}><span>plan</span></NavLink>
                </NavItem>
                <NavItem>
                    <NavLink to={'/trainer'}><span>trainer</span></NavLink>
                </NavItem>
            </NavTop>
            <NavBottom>
                <NavItem>
                    <NavLink to={'/supplements'}><span>supplement</span></NavLink>
                </NavItem>
                <NavItem><span>access</span></NavItem>
                <div onClick={props.contact}>
                    <NavItem><span>contact</span></NavItem>
                </div>
            </NavBottom>
        </nav>
    )
}

export default Nav;