import styled from "styled-components";

const Button = (props) => {
    const Button = styled.div`
        border: 1px solid #fff;
        padding: 22px 32px;
        transition: all 0.5s ease;
        cursor: pointer;
        display: inline-block;
        span {
            font-size: 25px;
            @media (max-width: 767px) {
                font-size: 20px;
            }
        }
        &:hover {
            background-color: #fff;
            span {
                color: #3E3E3E;
            }
        }
    `

    return (
        <Button>
            <span>{props.btn}</span>
        </Button>
    )
}

export default Button;